import React, { useEffect, useState } from "react";
import "./index.scss";
import Collection from "./Collection";
import axios from "axios";


const cats = [
  { name: "" },
  { name: "" },
  { name: "" },
  { name: "" },
  { name: "" },
];

function App() {
  const [categoryId, setCategoryId] = useState(0);
  const [page, setPage] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const [searchValue, setSearchValue] = useState("");
  const [collections, setCollections] = useState([]);
  const [numberPages, setNumberPages] = useState(3);
  const [notfound, setNotfound] = useState(false);

  const getData = async () => {
    try {
      setIsLoading(true);
      setNotfound(false);
      const category = categoryId ? `category=${categoryId}` : "";

      const numberElements = await axios(
        `https://67b9793c51192bd378dd8544.mockapi.io/questions?${category}&name=${searchValue}`
      );

      setNumberPages(Math.ceil(numberElements.data.length / 3));

      const result = await axios(
        `https://67b9793c51192bd378dd8544.mockapi.io/questions?page=${page}&limit=3&${category}&name=${searchValue}`
      );
      setCollections(result.data);
      if (Math.ceil(numberElements.data.length / 3) < 3) {
        setPage(1);
      }
      setIsLoading(false);
    } catch (error) {
      console.error(error);
      setNotfound(true);
      setNumberPages(1);
      setPage(1);
    }
  };

  useEffect(() => {
    getData();
  }, [categoryId, page, searchValue]);

  return (
    <div className="App">

 

      <h1>  </h1>
      <div className="top">
        <ul className="tags">
          {cats.map((obj, index) => (
            <li
              onClick={() => setCategoryId(index)}
              className={categoryId === index ? "active" : ""}
              key={obj.name}
            >
              {obj.name}
            </li>
          ))}
        </ul>
        <input
          type="search"
          onChange={(e) => setSearchValue(e.target.value)}
          value={searchValue}
          className="search-input"
          placeholder="  "
        />
      </div>
      <div className="content">
        {notfound ? (
          <h2>  </h2>
        ) : isLoading ? (
          <h2> ...</h2>
        ) : (
          collections
            // .filter((obj) =>
            //   obj.name.toLowerCase().includes(searchValue.toLowerCase())
            // )
            .map((obj, index) => (
              <Collection name={obj.name} key={index} images={obj.photos} />
            ))
        )}
      </div>

      <ul className="pagination">
        {[...Array(numberPages)].map((item, index) => (
          <li
            onClick={() => setPage(index + 1)}
            className={page === index + 1 ? "active" : ""}
            key={index}
          >
            {index + 1}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default App;
